(function() {
  'use strict';

  angular
    .module('detroitstartupjobs', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ui.router', 'ngMaterial', 'toastr', 'firebase']);

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .factory('Jobs', Jobs);

  /** @ngInject */
  function Jobs(FIREBASE_URL, $firebaseArray, $firebaseObject) {
    var itemsRef = new Firebase(FIREBASE_URL + "/jobs");

    var Jobs = $firebaseArray.$extend({
      filterJobsByCompany: function (company) {

      },
      filterJobsBySkill: function (skill) {

      },
      filterJobsBySalary: function (min, max) {

      }
    });

    return {
      "$array": Jobs(itemsRef),
      "$object": $firebaseObject(itemsRef)
    };
  }

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .controller('JobsController', JobsController);

  /** @ngInject */
  function JobsController ($log, $mdDialog, $document, $mdToast, Jobs) {

    var vm = this;
    vm.jobs = Jobs.$array;

    vm.goToSkill = function (chip) {
      $log.log('Clicked on Skill:');
      $log.log(chip);
    }

    vm.goToJob = function () {
      $log.log('Clicked on Job:');
    }

    vm.createJob = function (ev) {
      $mdDialog.show({
        controller: 'CreateDialogController',
        controllerAs: 'dialog',
        templateUrl: 'app/jobs/create.html',
        parent: angular.element($document.body),
        targetEvent: ev,
        clickOutsideToClose: true
      })
      .then( function (job) {
        var meta = {
          "created_on": moment().format(),
        }
        Jobs.$array.$add(_.merge(job, meta));
        $mdToast.show($mdToast.simple().content('Job Posted!').position('bottom right'));
      });
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .controller('CreateDialogController', CreateDialogController);

  /** @ngInject */
  function CreateDialogController ($log, $mdDialog) {

    var vm = this;

    vm.job = {
      "salary": 45,
      "skills": []
    }

    vm.cancel = function () {
      $mdDialog.cancel();
    }

    vm.save = function () {
      $mdDialog.hide(vm.job);
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('jobs', {
        url: '/jobs',
        templateUrl: 'app/jobs/index.html',
        controller: 'JobsController',
        controllerAs: 'home'
      });

    $urlRouterProvider.otherwise('/jobs');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .constant('malarkey', malarkey)
    .constant('moment', moment)
    .constant('FIREBASE_URL', 'https://detroitstartupjobs.firebaseio.com');

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig, $mdThemingProvider) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;

    // set our material design theme
    $mdThemingProvider.theme('default')
     .primaryPalette('cyan')
     .accentPalette('teal')
     .backgroundPalette('grey');
  }

})();

